.gallery {
    /* Mobile */
    display: grid;
    grid-template-columns: 1fr;

    gap: 16px;
    padding: 16px;
}

.gallery img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.gallery img:hover {
    transform: scale(1.05);
}

@media (min-width: 768px) {
    .gallery {
        grid-template-columns: repeat(3, 1fr);
        /* Desktop */
    }
}